*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  top: auto;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --font-hanken-grotesk: "Hanken Grotesk", sans-serif;
  --font-size-default: rem(16);
}

body {
  font-family: var(--font-hanken-grotesk);
  font-weight: 500;
  font-size: var(--font-size-default);
}

h1,
h2,
h3,
p {
  margin-top: 0;
  line-height: 1.1;
}

h1,
h2,
.summary__title {
  font-weight: 700;
}

h1,
h2 {
  margin-bottom: 1.5rem;
  font-size: 1.125rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--links);
}

:root {
  --subject-1: 0, 100%, 67%;
  --subject-2: 39, 100%, 56%;
  --subject-3: 166, 100%, 37%;
  --subject-4: 234, 85%, 45%;
  --bg-gradient: linear-gradient(
    225deg,
    hsl(252, 100%, 67%),
    hsl(241, 81%, 54%)
  );
  --bg-circle: linear-gradient(
    to bottom,
    hsla(256, 72%, 46%, 1),
    hsla(241, 72%, 46%, 0)
  );
  --white: hsl(0, 0%, 100%);
  --faded: hsl(241, 100%, 89%);
  --dark: hsl(224, 30%, 27%);
  --dark-faded: hsla(224, 30%, 27%, 0.5);
  --shadow: hsl(221, 100%, 96%);
  --links: hsl(252, 100%, 67%);
}

@media (min-width: 40em) {
  .content {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -5rem;
  }
}
@media (min-width: 71.875em) {
  .content {
    margin-top: 0;
  }
}

.summary {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 71.875em) {
  .summary {
    grid-template-columns: repeat(2, 1fr);
  }
}

.summary {
  max-width: 40rem;
}
@media (min-width: 40em) {
  .summary {
    border-radius: 1.25rem;
    overflow: hidden;
    box-shadow: 0.125rem 0.625rem 0.625rem var(--shadow);
  }
}

.summary__text,
.summary__overview {
  padding: 1.875rem 2.5rem;
}

.summary__text {
  border-radius: 0 0 2rem 2rem;
  background: var(--bg-gradient);
  text-align: center;
  color: var(--faded);
}
@media (min-width: 40em) {
  .summary__text {
    border-radius: 1.25rem;
  }
}

.summary__score {
  display: inline-block;
  margin-bottom: 1.875rem;
  border-radius: 50%;
  width: 8.75rem;
  height: 8.75rem;
  background: var(--bg-circle);
}
.summary__score--fraction {
  padding-top: 1.25rem;
  font-weight: 900;
  font-size: 3.5rem;
  letter-spacing: -0.125rem;
  color: var(--white);
}
.summary__score--total {
  font-size: 0.875rem;
}
@media (min-width: 71.875em) {
  .summary__score {
    width: 11.25rem;
    height: 11.25rem;
  }
  .summary__score--fraction {
    padding-top: 2.5rem;
  }
}

.summary__title {
  font-size: 1.5rem;
  color: var(--white);
}

.summary__overview {
  display: flex;
  flex-direction: column;
}
.summary__overview h2 {
  color: var(--dark);
}

.summary__items {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  row-gap: 1rem;
  margin-bottom: 1.875rem;
}

.summary__item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  column-gap: 0.625rem;
  border-radius: 0.5rem;
  padding: 1rem 0.625rem;
}
.summary__item--reaction {
  background-color: hsla(var(--subject-1), 0.05);
}
.summary__item--reaction .summary__subject {
  color: hsl(var(--subject-1));
}
.summary__item--memory {
  background-color: hsla(var(--subject-2), 0.05);
}
.summary__item--memory .summary__subject {
  color: hsl(var(--subject-2));
}
.summary__item--verbal {
  background-color: hsla(var(--subject-3), 0.05);
}
.summary__item--verbal .summary__subject {
  color: hsl(var(--subject-3));
}
.summary__item--visual {
  background-color: hsla(var(--subject-4), 0.05);
}
.summary__item--visual .summary__subject {
  color: hsl(var(--subject-4));
}

.summary__points--fraction {
  color: var(--dark);
}
.summary__points--total {
  color: var(--dark-faded);
}

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button {
  overflow: visible; /* 1 */
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button {
  text-transform: none; /* 1 */
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/* CUSTOM */
.btn {
  cursor: pointer;
  border: none;
  border-radius: 1.25rem;
  padding: 0.75rem 0;
  background: linear-gradient(var(--dark), var(--dark));
  color: var(--white);
  transition: all 150ms ease-in-out;
}
.btn:hover {
  background: var(--bg-gradient);
}

.footer {
  margin: 1.875rem 0;
}
.footer .attribution {
  text-align: center;
  font-size: 0.6875rem;
  color: #292929;
}